// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import { Button, Col, Row, Cell, Toast, Icon, Popup, Field, Dialog } from "vant"
Vue.use(Button).use(Col).use(Row).use(Toast).use(Icon).use(Popup).use(Dialog).use(Field)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  components: { App },
  template: '<App/>'
})



